<template>
  <div class="slide-show" @mouseover="pause" @mouseout="autoplay">
    <div class="slide-img">
      <a :href="slides[iNow].href">
        <transition name="slide-trans">
          <img v-if="show" :src="slides[iNow].src">
        </transition>
        <transition name="slide-trans-old">
          <img v-if="!show" :src="slides[iNow].src">
        </transition>
      </a>
    </div>
    <h2>{{slides[iNow].title}}</h2>
    <ul class="slide-pages">
      <li @click="shift(prevIndex)">&lt;</li>
      <li v-for="(item, index) in slides" @click="shift(index)">
        <a :class="{on: index === iNow}">{{index + 1}}</a>
      </li>
      <li @click="shift(nextIndex)">&gt;</li>
    </ul>
  </div>
</template>

<script>
  export default {
    props: ['slides', 'during'],
    data () {
      return {
        iNow: 0,
        show: true,
        timer: ''
      }
    },
    computed: {
      nextIndex () {
        if (this.iNow === this.slides.length-1) {
          return 0;
        } else {
          return this.iNow + 1;
        }
      },
      prevIndex () {
        if (this.iNow === 0) {
          return this.slides.length - 1;
        } else {
          return this.iNow - 1;
        }
      }
    },
    methods: {
      shift (index) {
        this.show = false;
        setTimeout(() => {
          this.show = true;
          this.iNow = index;
        }, 10);
      },
      autoplay () {
        this.timer = setInterval(() => {
          this.shift(this.nextIndex);
        }, this.during);
      },
      pause () {
        clearInterval(this.timer);
      }
    },
    mounted () {
      this.autoplay();
    }
  }
</script>

<style scoped>
.slide-trans-enter-active {
  transition: all .5s;
}
.slide-trans-enter {
  transform: translateX(900px);
}
.slide-trans-old-leave-active {
  transition: all .5s;
  transform: translateX(-900px);
}
.slide-show {
  position: relative;
  margin: 15px 15px 15px 0;
  width: 900px;
  height: 500px;
  overflow: hidden;
}
.slide-show h2 {
  position: absolute;
  width: 100%;
  height: 100%;
  color: #fff;
  background: #000;
  opacity: .5;
  bottom: 0;
  height: 30px;
  text-align: left;
  padding-left: 15px;
}
.slide-img {
  width: 100%;
}
.slide-img img {
  width: 100%;
  position: absolute;
  top: 0;
}
.slide-pages {
  position: absolute;
  bottom: 10px;
  right: 15px;
}
.slide-pages li {
  display: inline-block;
  padding: 0 10px;
  cursor: pointer;
  color: #fff;
}
.slide-pages li .on {
  text-decoration: underline;
}
</style>



