<template>
  <div class="index-wrap">
    <div class="index-left">
      <div class="index-left-block">
        <h2>全部产品</h2>
        <div v-for="(product, key, index) in dataList.productList" :key="index">
          <h3>{{product.title}}</h3>
          <ul>
            <li v-for="(item, index) in product.list" :key="index">
              <a :href="item.url" target="_blank">{{item.name}}<span class="hot-tag" v-if="item.hot">HOT</span></a>
            </li>
          </ul>
          <div class="hr" v-if="border(index, dataList.productList)"></div>
        </div>
      </div>
      <div class="index-left-block lastest-news">
        <h2>最新消息</h2>
        <ul>
          <li v-for="item in dataList.newsList">
            <a class="new-item" :href="item.url">{{item.name}}</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="index-right">
      <slider :slides="slides" :during="during"></slider>
      <div class="index-board-list">
        <div class="index-board-item" v-for="(item, index) in dataList.boardList" :class="[{'line-last': index % 2 !== 0}, 'index-board-'+item.id]">
          <div class="index-board-item-inner">
            <h2>{{item.title}}</h2>
            <p>{{item.description}}</p>
            <div class="index-board-button">
              <router-link :to="{path: '/detail/' + item.toKey}" class="button">立即购买</router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import slider from './slider'
export default {
  data () {
    return {
      during: 2000,
      slides: [
        {
          src: require('../assets/slideShow/pic1.jpg'),
          title: 'analysis',
          href: 'detail/analysis'
        },
        {
          src: require('../assets/slideShow/pic2.jpg'),
          title: 'count',
          href: 'detail/count'
        },
        {
          src: require('../assets/slideShow/pic3.jpg'),
          title: 'forecast',
          href: 'detail/forecast'
        },
        {
          src: require('../assets/slideShow/pic4.jpg'),
          title: 'publish',
          href: 'detail/publish'
        }
      ],
      dataList: {},
      border (index, obj) {  //计算productList里key的个数，删除最后一个产品下边框
        let size = 0;
        for (let key in obj) {
          if (obj.hasOwnProperty(key)) size++;
        }
        if (index === size-1) return false;
        return true;
      }
    }
  },
  created () {
    this.$http.get('static/data.json').then((res) => {
      this.dataList = res.data;
    }).catch((err) => {
      console.log(err);
    })
  },
  components: {
    slider
  }
}
</script>


<style scoped>
.index-wrap {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.index-left {
  float: left;
  width: 300px;
  text-align: left;
}
.index-right {
  float: left;
  width: 900px;
}
.index-left-block {
  margin: 15px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
}
.index-left-block .hr {
  margin-bottom: 20px;
}
.index-left-block h2 {
  background: #4fc08d;
  color: #fff;
  padding: 10px 15px;
  margin-bottom: 20px;
}
.index-left-block h3 {
  padding: 0 15px 5px 15px;
  font-weight: bold;
  color: #222;
}
.index-left-block ul {
  padding: 10px 15px;
}
.index-left-block li {
  padding: 5px;
}
.index-board-list {
  overflow: hidden;
}
.index-board-item {
  float: left;
  width: 400px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
  padding: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.index-board-item-inner {
  min-height: 125px;
  padding-left: 120px;
}
.index-board-car .index-board-item-inner{
  background: url(../assets/images/1.png) no-repeat;
}
.index-board-loud .index-board-item-inner{
  background: url(../assets/images/2.png) no-repeat;
}
.index-board-earth .index-board-item-inner{
  background: url(../assets/images/3.png) no-repeat;
}
.index-board-mountain .index-board-item-inner{
  background: url(../assets/images/4.png) no-repeat;
}
.index-board-item h2 {
  font-size: 18px;
  font-weight: bold;
  color: #000;
  margin-bottom: 15px;
}
.line-last {
  margin-right: 0;
}
.index-board-button {
  margin-top: 20px;
}
.lastest-news {
  min-height: 512px;
}
.hot-tag {
  background: red;
  color: #fff;
  margin-left: 5px;
  font-size: 12px;
}
.new-item {
  display: inline-block;
  width: 230px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



