<template>
  <div>
    <div class="popup-wrap">
      <div class="popup-cover" @click="closePopup" v-if="show"></div>
      <transition name="drop">
        <div class="popup-content" v-if="show">
          <p class="popup-close" @click="closePopup">x</p>
          <slot>empty</slot>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['show'],
    methods: {
      closePopup () {
        this.$emit('close');
      }
    }
  }
</script>

<style scoped>
.drop-enter-active {
  transition: all .5s ease;
}
.drop-leave-active {
  transition: all .3s ease;
}
.drop-enter {
  transform: translateY(-500px);
}
.drop-leave-active {
  transform: translateY(-500px);
}

.popup-wrap {
  position: fixed;
  width: 100%;
  height: 100%;
}
.popup-cover {
  background: #000;
  opacity: .3;
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.popup-content {
  width: 50%;
  position: fixed;
  max-height: 50%;
  overflow: auto;
  background: #fff;
  top: 20%;
  left: 50%;
  margin-left: -25%;
  z-index: 10;
  border: 2px solid #464068;
  padding: 2%;
  line-height: 1.6;
}
.popup-close {
  position: absolute;
  right: 5px;
  top: 5px;
  width: 20px;
  height: 20px;
  text-align: center;
  cursor: pointer;
}
.popup-close:hover {
  color: #4fc08d;
}
</style>



